import React from "react";
import { useForm, SubmitHandler, SubmitErrorHandler } from "react-hook-form";
import styles from "./SendForm.module.scss";

interface DataForm {
  customerName: string;
  phoneNumber: number;
  message: string;
}

export const SendForm: React.FC = () => {
  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm<DataForm>();

  const onSubmit: SubmitHandler<DataForm> = (data) => {
    console.log(data);
    reset();
  };

  const onError: SubmitErrorHandler<DataForm> = (data) => {
    console.log(data);
  };

  return (
    <form
      className={styles["send-form"]}
      onSubmit={handleSubmit(onSubmit, onError)}
    >
      <div className="send-form__inner">
        <input
          {...register("customerName", {
            required: "  '",
            validate: (value) => {
              return value.trim() === value
                ? true
                : "   ";
            },
            minLength: {
              value: 2,
              message: " 2 ",
            },
          })}
          className={styles["send-form__input"]}
          placeholder="  "
          type="text"
        />
        {errors?.customerName && (
          <p className="send-form__error">
            {errors?.customerName?.message || ""}
          </p>
        )}
      </div>

      <div className="send-form__inner">
        <input
          {...register("phoneNumber", {
            required: "  '",
            pattern: {
              value: /^[0-9]*$/,
              message: " ",
            },
            minLength: {
              value: 10,
              message: " 10 ",
            },
          })}
          className={styles["send-form__input"]}
          placeholder=" "
          type="tel"
        />
        {errors?.phoneNumber && (
          <p className="send-form__error">
            {errors?.phoneNumber?.message || ""}
          </p>
        )}
      </div>

      <textarea
        {...register("message")}
        className={`${styles["send-form__input"]} ${styles["send-form__input--message"]}`}
        name="message"
        placeholder=""
      />

      <button
        type="submit"
        className={`${styles["send-form__button"]} button-send`}
      >
        
      </button>
    </form>
  );
};
